.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;  
  font-size: 1.8rem;
  color: #fff; 
}

/* 
.App-intro {
  font-size: large;
} */

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.Header-comp {     
  margin-top: 20px;
  text-shadow: 2px 2px 8px #000;
}

.History-colors {
  font-size: 1.4rem;
  display: inline-block;
  padding: 0.8rem;
  border-radius: 0.6rem;
  margin-bottom: 0.8rem;   
}

.Colors {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0 2rem;  
  -ms-flex-pack: center;  
      justify-content: center;   
}

.Color-square {
  padding: 1.6rem;
  margin: 0.5rem;
  border-radius: 0.6rem;  
}

.Btn-section {  
  padding-bottom: 2rem;
}

.Btn {
  padding: 1rem; 
  background-color: greenyellow;  
}

.Btn:first-child {
  margin-right: 1rem;
}

.Btn-hidden {
  display: none;
}

.Welcome {
  padding: 3rem 0;    
  font-size: 1.6rem;
}


.Welcome-input {
  display: block;
  margin: 0 auto;
  padding: 0.4rem;
  margin-top: 1.4rem;
  font-size: 1rem;
}



